.ribbon {
  position: absolute;
  inset-inline-end: -5px;
  inset-block-start: -5px;
  z-index: 1;
  overflow: hidden;
  width: 75px;
  height: 75px;
  text-align: right;
}

.ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: linear-gradient(#f8463f 0%, #f8463f 100%);
  box-shadow: 0 3px 10px -5px rgb(0, 0, 0);
  position: absolute;
  inset-block-start: 19px;
  inset-inline-end: -21px;
}

.ribbon span::before {
  content: "";
  position: absolute;
  inset-inline-start: 0px;
  inset-block-start: 100%;
  z-index: -1;
  border-left: 3px solid rgb(var(--danger-rgb));
  border-inline-end: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-block-start: 3px solid rgb(var(--danger-rgb));
}

.ribbon span::after {
  content: "";
  position: absolute;
  inset-inline-end: 0px;
  inset-block-start: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-inline-end: 3px solid rgb(var(--danger-rgb));
  border-bottom: 3px solid transparent;
  border-block-start: 3px solid rgb(var(--danger-rgb));
}

