  position: absolute;
  inset-block-start: -6.1px;
  inset-inline-end: 10px;
  color: #fff;
  z-index: 1;
}

.ribbon1 span {
  position: relative;
  display: block;
  text-align: center;
  background: rgb(var(--danger-rgb));
  font-size: 14px;
  line-height: 1;
  padding: 12px 8px 10px;
  border-start-end-radius: 8px;
}

.ribbon1 span:before {
  height: 6px;
  width: 6px;
  inset-inline-start: -6px;
  inset-block-start: 0;
  background: rgb(var(--danger-rgb));
}

.ribbon1 span:before,
.ribbon1 span:after {
  position: absolute;
  content: "";
}

.ribbon1 span:after {
  height: 6px;
  width: 8px;
  inset-inline-start: -8px;
  inset-block-start: 0;
  border-radius: 8px 8px 0 0;
  background: rgb(var(--danger-rgb));
}

.ribbon1:after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
