/* Sidemenu Layout Syles */
@media (max-width: 991.98px) {
    .sidemenu-layout-styles {
      display: none;
    }
  }
  
  /* Sidemenu Layout Syles */
  
  /* Respoonsive Logo Styles */
  @media (max-width: 991.98px) {
    .app-header .horizontal-logo .header-logo img {
      height: 2rem;
      line-height: 2rem;
    }
  }
  
  /* Respoonsive Logo Styles */
  
  /* Dark Mode */
  
  //dark mode styles start
  [data-theme-mode="dark"] {
    .app-sidebar .main-sidebar-header {
      .header-logo {
        .desktop-dark {
          display: block;
        }
  
        .desktop-logo,
        .toggle-logo,
        .toggle-dark,
        .desktop-white,
        .toggle-white {
          display: none;
        }
      }
    }
  
    .btn-close {
      filter: invert(1);
    }
  
    .alert {
      .btn-close {
        filter: unset;
      }
    }
  
    .dropdown-menu {
      box-shadow: 0 1rem 1.125rem rgba(40, 40, 40, 0.15);
    }
  
    button.gridjs-sort,
    button.gridjs-sort-desc,
    button.gridjs-sort-asc {
      filter: invert(1);
    }
  
    .choices__list--dropdown,
    .choices__list[aria-expanded] {
      box-shadow: rgba(10, 10, 10, 0.2) 0 0.5rem 1.5rem !important;
    }
    &[data-menu-styles="light"] {
      .app-sidebar {
        .side-menu__label1 {
          border-block-end: 1px solid rgba(255, 255, 255, 0.1) !important;
          a {
            color: #fff;
          }
        }
        .slide.has-sub .slide-menu {
          --menu-prime-color: rgb(171, 184, 199);
        }
      }
    }
    
    &[data-header-styles="light"]{
      .app-header .header-content-left{
          .main-header-search{
              .form-control{
                  border-color: #e9edf6;
                  &:active,&:focus{
                      border-color: #e9edf6;
                      color: #2e384d;
                  }
                  &::placeholder{
                      color:rgba(0, 0, 0, 0.6);
                  }
                  color: rgb(222, 222, 228);
              }
          } 
          .btn.btn-outline-light{
            background-color: transparent !important;
            border-color:rgb(242,242,249) !important;
            color: #2e384d !important;
            &:hover{
                  background-color: transparent !important;
                  border-color:rgb(242,242,249) !important;
              }
            }
            .btn-outline-light.dropdown-toggle.show{
                background-color: transparent !important;
                color: $dark;
            }
      
      }

  }
  }
  
  //dark mode styles end
  
  /* Dark Mode */
  
  /* Responsive Menu Styles */
  
  // responsive header logo styles start
  [data-header-styles="dark"],
  [data-header-styles="transparent"][data-theme-mode="dark"] {
    @media (max-width: 991.98px) {
      .app-header .horizontal-logo .header-logo {
        .desktop-logo,
        .desktop-dark,
        .toggle-logo,
        .desktop-white,
        .toggle-white {
          display: none;
        }
        .toggle-dark {
          display: block;
        }
      }
    }
  }
  [data-header-styles="color"],
  [data-header-styles="gradient"] {
    @media (max-width: 991.98px) {
      .app-header .horizontal-logo .header-logo {
        .desktop-logo,
        .desktop-dark,
        .toggle-logo,
        .desktop-white,
        .toggle-dark {
          display: none;
        }
        .toggle-white {
          display: block;
        }
      }
    }
  }
  
  [data-header-styles="light"],
  [data-header-styles="transparent"][data-theme-mode="light"] {
    @media (max-width: 991.98px) {
      .app-header .horizontal-logo .header-logo {
        .toggle-logo {
          display: block;
        }
        .desktop-logo,
        .toggle-dark,
        .desktop-dark,
        .desktop-white,
        .toggle-white {
          display: none;
        }
      }
    }
  }
  
  // responsive header logo styles end
  
  // toggle close start
  [data-toggled="close"] {
    @media (max-width: 991.98px) {
      .app-sidebar {
        transition: all 300ms ease;
        transform: translateX(-15rem);
      }
  
      .app-header {
        padding-inline-start: 0rem;
  
        .horizontal-logo {
          display: block;
        }
      }
  
      &[dir="rtl"] {
        .app-sidebar {
          transition: all 300ms ease;
          transform: translateX(15rem);
        }
      }
    }
  }
  
  // toggle close end
  
  //toggle open start
  [data-toggled="open"] {
    @media (max-width: 991.98px) {
      .app-sidebar {
        transition: all 300ms ease;
        transform: translateX(0rem);
      }
  
      .app-header {
        padding-inline-start: 0rem;
  
        .horizontal-logo {
          display: block;
        }
      }
    }
  }
  
  //toggle open end
  
  /* Responsive Menu Styles */
  
  /* Page Styles */
  
  // classic start
  [data-page-style="classic"] {
    --default-border: #e1e9e8;
    --header-border-color: #e1e9e8;
    --menu-border-color: #e1e9e8;
    &[data-theme-mode="dark"] {
      --default-border: rgba(255, 255, 255, 0.1);
      --header-border-color: rgba(255, 255, 255, 0.1);
      --menu-border-color: rgba(255, 255, 255, 0.1);
    }
    body {
      background-color: $custom-white;
    }
  
    .card.custom-card {
      box-shadow: none;
      border: 1px solid $default-border;
    }
  
    .app-sidebar,
    .footer,
    .app-header,
    .app-sidebar .main-sidebar {
      box-shadow: none;
    }
  
    &[data-nav-layout="horizontal"] {
      .app-sidebar {
        border-block-end: 1px solid $default-border;
      }
    }
  }
  // page styles classic end
  
  // page styles modern start
  [data-page-style="modern"] {
    --light-rgb: 255, 255, 255;
    --custom-white: rgb(243, 246, 248);
    --default-border: #e6ebf1;
    --input-border: rgba(0, 0, 0, 0.1);
    .dropdown-menu {
      background-color: rgb(255, 255, 255);
    }
    body {
      background-color: #fff;
    }
    .card.custom-card {
      box-shadow: none;
      background-color: rgb(243, 246, 248);
    }
    &[data-theme-mode="dark"] {
      --light-rgb: 20, 20, 20;
      --custom-white: rgb(var(--body-bg-rgb));
      --default-border: rgb(255, 255, 255, 0.1);
      .card.custom-card {
        box-shadow: none;
        background-color: var(--custom-white);
      }
      .dropdown-menu {
        background-color: $light;
      }
      body {
        background-color: var(--default-body-bg-color);
      }
      .btn-light {
        background-color: rgba(255, 255, 255, 0.1) !important;
        border-color: rgba(255, 255, 255, 0.1) !important;
        &:hover,
        &:focus,
        &:active {
          background-color: rgba(255, 255, 255, 0.1) !important;
          border-color: rgba(255, 255, 255, 0.1) !important;
        }
      }
    }
    .app-sidebar,
    .footer,
    .app-header,
    .app-sidebar .main-sidebar {
      box-shadow: none;
    }
  }
  // page styles modern end
  
  /* Page Styles */
  
  /* Layout Width Styles */
  
  // boxed start
  [data-width="boxed"] {
    @media (min-width: 1400px) {
      body {
        background-color: $default-background;
      }
  
      .page {
        width: 1400px;
        margin: 0 auto;
        background-color: $default-body-color;
        position: relative;
        box-shadow: 0 0 1rem $black-1;
  
        .app-header {
          width: 1400px;
          margin: 0 auto;
        }
  
        .app-sidebar {
          inset-inline-start: auto;
        }
      }
  
      &[data-nav-layout="horizontal"] {
        .app-sidebar {
          width: 1400px !important;
        }
      }
  
      &[data-page-style="classic"] {
        .page {
          background-color: $custom-white;
        }
  
        .card {
          box-shadow: none;
        }
      }
  
      &[data-theme-mode="dark"] {
        --default-body-bg-color: rgba(var(--body-bg-rgb), 0.85);
  
        body {
          background-color: rgba(var(--body-bg-rgb), 0.6);
        }
      }
  
      &[data-vertical-style="detached"] {
        .page {
          .app-header {
            width: 1400px;
          }
  
          width: 1400px;
          box-shadow: none;
        }
  
        &[data-toggled="detached-close"] {
          .app-content {
            margin-inline-start: 5rem;
          }
        }
  
        .app-header .main-header-container {
          width: 99%;
        }
  
        .app-content {
          margin-inline-start: 15rem;
        }
      }
  
      &[data-vertical-style="doublemenu"] {
        .app-sidebar .main-sidebar {
          height: 100%;
        }
  
        .app-sidebar .simplebar-content-wrapper {
          position: initial;
        }
  
        .app-sidebar .simplebar-mask {
          position: inherit;
        }
      }
      .mail-info-body {
        max-height: calc(100vh - 19.3rem);
      }
      #Invoice {
        .list-group-item > a > div {
          display: block !important;
        }
      }
      .leads-source-labels {
        .col {
          flex: 100%;
        }
      }
    }
  }
  
  // boxed end
  
  /* Layout Width Styles */
  
  /* Menu Positions Styles */
  
  //scrollable start
  [data-menu-position="scrollable"] {
    &[data-nav-layout="vertical"] {
      .app-sidebar {
        position: absolute;
  
        .main-sidebar-header {
          position: absolute;
        }
      }
    }
  
    &[data-nav-layout="horizontal"] {
      .app-sidebar {
        position: relative;
        z-index: 102;
      }
  
      .app-content {
        margin-block-start: 4rem;
      }
  
      &[data-header-position="scrollable"] {
        .app-sidebar {
          position: relative;
        }
  
        .app-header {
          position: relative;
        }
      }
    }
  
    @media (max-width: "991.98px") {
      &[data-nav-layout="horizontal"] {
        .app-sidebar {
          position: absolute;
        }
  
        .main-content {
          margin-block-start: 0;
        }
  
        .main-sidebar {
          height: 100%;
        }
      }
    }
  
    @media (min-width: 992px) {
      .app-content {
        min-height: calc(100vh - 2.5rem);
      }
    }
  }
  
  //scrollable end
  
  /* Menu Positions Styles*/
  
  /* Header Positions Styles */
  
  //scrollable start
  [data-header-position="scrollable"] {
    &[data-nav-layout="vertical"] {
      .app-header {
        position: absolute;
      }
    }
  
    &[data-nav-layout="horizontal"] {
      .app-sidebar {
        position: relative;
        top: 0;
  
        &.sticky {
          position: sticky;
          top: 0;
        }
      }
  
      .app-content {
        margin-block-start: 0;
      }
  
      .app-header {
        position: relative;
      }
  
      &[data-menu-position="scrollable"] {
        .app-sidebar {
          position: relative;
        }
  
        .app-header {
          position: relative;
        }
      }
  
      &[data-width="boxed"] {
        .app-header {
          margin: 0;
        }
      }
    }
  
    @media (max-width: "991.98px") {
      &[data-nav-layout="horizontal"] {
        .app-sidebar {
          position: absolute !important;
          z-index: 102;
        }
  
        .main-content {
          padding-block-start: 0;
        }
  
        &[data-menu-position="fixed"] {
          .app-sidebar {
            position: fixed !important;
  
            .main-sidebar {
              margin-block-start: 3.72rem;
            }
  
            &.sticky {
              .main-sidebar {
                margin-block-start: 0;
              }
            }
          }
        }
      }
  
      &[data-nav-layout="vertical"] {
        .app-sidebar.sticky {
          inset-block-start: 0;
        }
      }
    }
  }
  
  //scrollable end
  
  /* Header Positions Styles*/
  
  /* Menu Colors Styles*/
  
  //menu-light start
  [data-menu-styles="light"] {
    --menu-bg: #fff;
    --menu-prime-color: #536485;
    --menu-border-color: #f3f3f3;
  
    &[data-nav-layout="vertical"] {
      .app-sidebar .main-sidebar-header .header-logo {
        .desktop-logo {
          display: block;
        }
  
        .desktop-dark,
        .toggle-dark,
        .toggle-logo,
        .desktop-white,
        .toggle-white {
          display: none;
        }
      }
    }
  
    .side-menu__label1 a {
      color: #222528;
      font-weight: 600;
    }
  
    .side-menu__label1 {
      border-block-end: 1px solid #f3f3f3 !important;
    }

  
    @media (min-width: 992px) {
      &[data-vertical-style="doublemenu"] .slide.has-sub .slide-menu {
        border-inline-end: 1px solid #f3f3f3;
      }
    }
  }
  
  //menu-light end
  
  //menu-dark start
  [data-menu-styles="dark"] {
    --menu-bg: #{var(--custom-white)};
    --menu-prime-color: rgba(255, 255, 255, 0.6);
    --menu-border-color: rgba(255, 255, 255, 0.1);
  
    &[data-nav-layout="vertical"] {
      .app-sidebar .main-sidebar-header .header-logo {
        .desktop-dark {
          display: block;
        }
  
        .desktop-logo,
        .toggle-dark,
        .toggle-logo,
        .desktop-white,
        .toggle-white {
          display: none;
        }
      }
  
      &[data-vertical-style="overlay"][data-toggled="icon-overlay-close"] {
        .app-sidebar .main-sidebar-header .header-logo {
          .toggle-dark {
            display: block;
          }
  
          .desktop-logo,
          .desktop-dark,
          .toggle-logo,
          .desktop-white,
          .toggle-white {
            display: none;
          }
        }
  
        &[data-icon-overlay="open"] {
          .app-sidebar .main-sidebar-header .header-logo {
            .desktop-dark {
              display: block;
            }
  
            .desktop-logo,
            .toggle-dark,
            .toggle-logo,
            .desktop-white,
            .toggle-white {
              display: none;
            }
          }
        }
      }
    }
  
    .side-menu__label1 a {
      color: rgba(255, 255, 255);
      font-weight: 600;
    }
  
    .side-menu__label1 {
      border-block-end: 1px solid rgba(255, 255, 255, 0.1) !important;
    }
  
    .app-sidebar .slide-menu.child1 .side-menu__item:hover,
    .app-sidebar .slide-menu.child2 .side-menu__item:hover,
    .app-sidebar .slide-menu.child3 .side-menu__item:hover {
      color: #fff;
    }
  
    .app-sidebar .slide-menu.child1 .side-menu__item:hover:before,
    .app-sidebar .slide-menu.child2 .side-menu__item:hover:before,
    .app-sidebar .slide-menu.child3 .side-menu__item:hover:before {
      opacity: 1;
    }
  
    // .app-sidebar .side-menu__item.active,
    // .app-sidebar .side-menu__item:hover {
    //   color: #fff;
    // }
  
    .app-sidebar .slide-menu.child1 .side-menu__item.active:before,
    .app-sidebar .slide-menu.child2 .side-menu__item.active:before,
    .app-sidebar .slide-menu.child3 .side-menu__item.active:before {
      border-color: $white;
    }
  
    &[data-theme-mode="light"] {
      --menu-bg: #011626;
  
      &[data-bg-img="bgimg1"] {
        --menu-bg: url(../images/menu-bg-images/bg-img1.jpg);
      }
  
      &[data-bg-img="bgimg2"] {
        --menu-bg: url(../images/menu-bg-images/bg-img2.jpg);
      }
  
      &[data-bg-img="bgimg3"] {
        --menu-bg: url(../images/menu-bg-images/bg-img3.jpg);
      }
  
      &[data-bg-img="bgimg4"] {
        --menu-bg: url(../images/menu-bg-images/bg-img4.jpg);
      }
  
      &[data-bg-img="bgimg5"] {
        --menu-bg: url(../images/menu-bg-images/bg-img5.jpg);
      }
    }
  
    @media (min-width: 992px) {
      &[data-nav-layout="horizontal"] .main-menu-container .slide-left,
      &[data-nav-layout="horizontal"] .main-menu-container .slide-right {
        background-color: rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.1);
        svg {
          fill: #fff;
        }
      }
  
      &[data-vertical-style="doublemenu"] .slide.has-sub .slide-menu {
        border-inline-end: 1px solid rgba(255, 255, 255, 0.1);
      }
    }
  
    &[data-theme-mode="light"] {
      --menu-bg: #111c43;
      --menu-prime-color: #a3aed1;
    }
  }
  
  //menu-dark end
  
  //color and gradient menu color mixin start
  @mixin color-gradient($color) {
    color: $color !important;
  
    .side-menu__icon,
    .side-menu__label,
    .side-menu__angle {
      color: $color !important;
      fill: $color !important;
    }
    
  }
  
  //color and gradient menu color mixin end
  
  //menu-color start
  [data-menu-styles="color"] {
    --menu-bg: #{$primary};
    --menu-prime-color: rgba(255, 255, 255, 0.6);
    --menu-border-color: rgba(255, 255, 255, 0.1);
  
    &[data-nav-layout="vertical"] {
      .app-sidebar {
        .main-sidebar-header .header-logo {
          .desktop-white {
            display: block;
          }
  
          .desktop-logo,
          .desktop-dark,
          .toggle-dark,
          .toggle-logo,
          .toggle-white {
            display: none;
          }
        }
        .slide-menu {
            &.child1,
            &.child2,
            &.child3 {
            .side-menu__item {
                &:hover,
                &.active {
                &:before {
                    color: $white;
                }
                }
            }
            }
        }
      }
  
      &[data-vertical-style="overlay"][data-toggled="icon-overlay-close"] {
        .app-sidebar .main-sidebar-header .header-logo {
          .toggle-white {
            display: block;
          }
  
          .desktop-logo,
          .desktop-dark,
          .toggle-dark,
          .toggle-logo {
            display: none;
          }
        }
  
        &[data-icon-overlay="open"] {
          .app-sidebar .main-sidebar-header .header-logo {
            .desktop-white {
              display: block;
            }
  
            .desktop-logo,
            .desktop-dark,
            .toggle-dark,
            .toggle-white,
            .toggle-logo {
              display: none;
            }
          }
        }
      }
  
      &[data-nav-style="icon-click"][data-toggled="icon-click-closed"],
      &[data-nav-style="icon-hover"][data-toggled="icon-hover-closed"] {
        .app-sidebar .main-sidebar-header .header-logo {
          .toggle-white {
            display: block !important;
          }
  
          .desktop-logo,
          .desktop-dark,
          .desktop-white,
          .toggle-white,
          .toggle-logo {
            display: none;
          }
        }
      }
    }
  
    .side-menu__item {
      &.active {
        @include color-gradient($white);
      }
  
      &:hover {
        @include color-gradient($white);
      }
    }
  
    .slide-menu {
      --menu-bg: #{$primary};
    }
  
    .side-menu__label1 a {
      color: rgba(255, 255, 255);
      font-weight: 600;
    }
  
    .side-menu__label1 {
      border-block-end: 1px solid rgba(255, 255, 255, 0.1) !important;
    }

  
    @media (min-width: 992px) {
      &[data-nav-layout="horizontal"] .main-menu-container .slide-left,
      &[data-nav-layout="horizontal"] .main-menu-container .slide-right {
        background-color: rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.1);
  
        svg {
          fill: $primary;
        }
      }
  
      &[data-vertical-style="doublemenu"] .slide.has-sub .slide-menu {
        border-inline-end: 1px solid rgba(255, 255, 255, 0.1);
      }
    }
  }
  
  //menu-color end
  
  //menu-gradient start
  [data-menu-styles="gradient"] {
    --menu-bg: linear-gradient(to bottom, #{$primary} 0%, #00b9da 100%);
    --menu-prime-color: rgba(255, 255, 255, 0.7);
    --menu-border-color: rgba(255, 255, 255, 0.1);
  
    &[data-nav-layout="horizontal"] {
      --menu-bg: linear-gradient(to right, #{$primary} 0%, #00b9da 100%);
    }
  
    .app-sidebar .main-sidebar-header {
      background: $primary;
    }
  
    &[data-nav-layout="vertical"] {
      .app-sidebar .main-sidebar-header .header-logo {
