          .toggle-white {
            display: block;
          }
  
          .desktop-logo,
          .desktop-dark,
          .desktop-white,
          .toggle-dark,
          .toggle-logo {
            display: none;
          }
        }
  
        &[data-icon-overlay="open"] {
          .app-sidebar .main-sidebar-header .header-logo {
            .desktop-white {
              display: block;
            }
  
            .desktop-logo,
            .desktop-dark,
            .toggle-dark,
            .toggle-white,
            .toggle-logo {
              display: none;
            }
          }
        }
      }
      &[data-nav-style="icon-click"][data-toggled="icon-click-closed"],
      &[data-nav-style="icon-hover"][data-toggled="icon-hover-closed"] {
        .app-sidebar .main-sidebar-header .header-logo {
          .toggle-white {
            display: block !important;
          }
  
          .desktop-logo,
          .desktop-dark,
          .desktop-white,
          .toggle-white,
          .toggle-logo {
            display: none;
          }
        }
      }
    }
  
    .side-menu__item {
      &.active {
        @include color-gradient($white);
      }
  
      &:hover {
        @include color-gradient($white);
      }
    }
  
    .slide-menu {
      --menu-bg: #{$primary};
    }
  
    .side-menu__label1 a {
      color: rgba(255, 255, 255);
      font-weight: 600;
    }
  
    .side-menu__label1 {
