/* Start::Blockquote */
.blockquote-container {
    padding: 1.25rem;
    border: 1px solid $default-border;
    border-block-start: 4px solid $default-border;
    border-radius: $default-radius;
    position: relative;

    &:before {
        position: absolute;
        content: "\f6b0";
        font-family: bootstrap-icons !important;
        width: 1.5rem;
        height: 1.5rem;
        font-size: 0.813rem;
        font-weight: 600;
        border: 1px solid $default-border;
        background-color: $custom-white;
        top: -0.875rem;
        align-items: center;
        display: flex;
        padding: 0px;
        justify-content: center;
        border-radius: 3.125rem;
        inset-inline-start: 50%;
        box-shadow: 0px 0.25rem 1rem rgba(0, 0, 0, 0.1);
    }
}

.blockquote.custom-blockquote {
    font-size: 0.85rem;
    font-weight: 600;
    padding: 1rem;
    border-radius: $default-radius;
    position: relative;

    &:before {
        content: "\f6b0";
        font-family: bootstrap-icons !important;
        z-index: 0;
        font-size: 4rem;
        position: absolute;
        inset-block-start: -1.5rem;
        inset-inline-start: -0.25rem;
    }

    .quote-icon {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3.125rem;
        position: absolute;
        inset-block-start: -1rem;
        inset-inline-end: -1rem;
        background-color: $custom-white;

        i {
            font-size: 1.75rem;
            font-weight: 500;
        }
    }

    &.primary {
        background-color: $primary-01;
        border-inline-start: 2px solid $primary;
        border-block-start: 2px solid $primary;

        .quote-icon i {
            color: $primary;
        }

        &:before {
            color: $primary-01;
        }
    }

    &.secondary {
        background-color: rgba(var(--secondary-rgb), 0.1);
        border-inline-start: 2px solid $secondary;
        border-block-start: 2px solid $secondary;

        .quote-icon i {
            color: $secondary;
        }

        &:before {
            color: rgba(var(--secondary-rgb), 0.1);
        }
    }

    &.info {
        background-color: rgba(var(--info-rgb), 0.1);
        border-inline-start: 2px solid $info;
        border-block-start: 2px solid $info;

        .quote-icon i {
            color: $info;
        }

        &:before {
            color: rgba(var(--info-rgb), 0.1);
        }
    }

    &.warning {
        background-color: rgba(var(--warning-rgb), 0.1);
        border-inline-start: 2px solid $warning;
        border-block-start: 2px solid $warning;

        .quote-icon i {
            color: $warning;
        }

        &:before {
            color: rgba(var(--warning-rgb), 0.1);
        }
    }

    &.success {
        background-color: rgba(var(--success-rgb), 0.1);
        border-inline-start: 2px solid $success;
        border-block-start: 2px solid $success;

        .quote-icon i {
            color: $success;
        }

        &:before {
            color: rgba(var(--success-rgb), 0.1);
        }
    }

    &.danger {
        background-color: rgba(var(--danger-rgb), 0.1);
        border-inline-start: 2px solid $danger;
        border-block-start: 2px solid $danger;

        .quote-icon i {
            color: $danger;
        }

        &:before {
            color: rgba(var(--danger-rgb), 0.1);
        }
    }
}

/* End::Blockquote */

